import { apiKey } from "../env";
export const headlinesURL = `http://newsapi.org/v2/top-headlines?country=in&apiKey=${apiKey}`;

let day = new Date();
day.setMonth(day.getMonth() - 1);
let dateString = day.toISOString().slice(0, 10);
export const getSearchURL = function (searchKey = "") {
  return `http://newsapi.org/v2/everything?q=${searchKey}&from=${dateString}&sortBy=popularity&apiKey=${apiKey}`;
};
